nav {
  margin: 100px;
  background-color: #e64a19;
  display: inline-block;
}

nav ul {
  padding: 0;
  margin: 0;
  list-style: none;
  position: relative;
}

nav ul li {
  margin: 0px -7px 0 0;
  display: inline-block;
  background-color: #ff8c00;
}
nav a {
  display: block;
  padding: 0 10px;
  color: #fff;
  font-size: 20px;
  line-height: 60px;
  text-decoration: none;
}
nav a:hover {
  background-color: #000000;
}

nav ul ul {
  display: none;
  position: absolute;
  top: 100%;
}
nav ul li:hover > ul {
  display: inherit;
  margin-left: 70px;
}
nav ul ul li {
  min-width: 170px;
  float: none;
  display: list-item;
  position: relative;
}
nav ul ul ul {
  position: absolute;
  top: 0;
  left: 59%;
  margin-left: 100px;
}
